// Utility Functions
// mapObject, values, keys, omit, and isFunction

function mapObject(obj, callback, thisContext) {
  return Object.keys(obj).map(function(key) {
    return (callback.bind(thisContext ? thisContext : obj))(obj[key], key);
  });
}

function values(obj) {
  return Object.keys(obj).map(function(key) {
    return obj[key];
  });
}

function keys(obj) {
  return Object.keys(obj);
}

function omit(obj, omitKeys) {
  if (typeof omitKeys === 'string') omitKeys = [omitKeys];
  var retObj = {};
  keys(obj)
    .filter(function(key) {
      return (omitKeys.indexOf(key) === -1);
    }).forEach(function(key) {
      retObj[key] = obj[key];
    });
  return retObj;
}

function isFunction(sut) {
  return (typeof(sut) === 'function');
}

// Backbone View Mediator

var BackboneViewMediator = (function() {

  function setupFactories(views) {
    var retVal = {};
    mapObject(views, function(factory, name) {
      retVal[name] = {
        factory: isFunction(factory) ? factory : function() {
          return factory;
        },
        isRendered: false,
        mountedAt: null
      };
    });
    return retVal;
  }

  // Constructor

  function BackboneViewMediator(params) {
    // save element selector in case DOM is not ready at initialization
    // and this.el needs to be set later
    this.selector = params.el

    this.currentLayout = null;
    this.el = document.querySelector(params.el);

    this.views = setupFactories(params.views);
    this.layout = params.layout;
    this.layoutIsRendered = false;
  }

  BackboneViewMediator.prototype._checkHasElement = function(){
    if (typeof this.el === 'undefined' || this.el === null) {
      this.el = document.querySelector(this.selector)
    }
  }

  BackboneViewMediator.prototype.render = function(viewsToRender) {
    if (!this.layoutIsRendered) this.renderLayout();

    if (arguments.length === 0) return;

    // remove views that aren't specified in the render config object,
    // do this by getting the view names from the renderConfig, then
    // passing in all the views registered in the ViewMediator, with those
    // omitted. Ugly function, sorry.
    this.removeViews(
      keys(
        omit(
          this.views,
          Object.keys(viewsToRender)
        )
      )
    );

    this.renderViews(viewsToRender);
  };

  BackboneViewMediator.prototype.remove = function() {
    this.removeViews(keys(this.views));
    this.el.innerHTML = '';
    this.layoutIsRendered = false;
  };

  BackboneViewMediator.prototype._checkViewIsRegistered = function(viewName) {
    if (Object.keys(this.views).indexOf(viewName) === -1) {
      var err = viewName + ' is not a registered view';
      throw new Error(err);
    } else {
      return true;
    }
  };

  BackboneViewMediator.prototype.removeViews = function(views) {
    if (typeof views === 'string') views = [views];

    views.forEach(function(_view) {

      this._checkViewIsRegistered(_view);

      var view = this.views[_view];

      // set view.isRendered to false
      view.isRendered = false;

      // tell the view instance to call its remove function
      if (view.controller) view.controller.remove();

      // setting the controller to null tells the VM to construct a new one on next render
      view.controller = null;

      // view is no longer mounted
      view.mountedAt = null;

    }, this);
  };

  BackboneViewMediator.prototype.renderLayout = function() {
    this._checkHasElement();
    this.el.innerHTML = this.layout;
    this.layoutIsRendered = true;
  };

  function initializeView(view, el, viewParams){
    view.controller = new view.factory(viewParams);
    view.mountedAt = el;
    if (view.controller.setElement) view.controller.setElement(el);
    view.controller.render();
    view.isRendered = true;

    return view;
  }

  BackboneViewMediator.prototype._removeViewAtRegion = function(el) {
    mapObject(this.views, function(view, viewName){
      if (view.mountedAt === el) {
        this.removeViews(viewName);
      }
    }, this);
  }

  BackboneViewMediator.prototype.renderViews = function(newViews) {
    this._checkHasElement()

    mapObject(newViews, function(config, viewName) {
      var view = this.views[viewName],
          viewParams = null,
          el = null

      if (typeof config === 'string') {
        el = config;
      } else {
        viewParams = config.params;
        el = config.el;
      }

      this._checkViewIsRegistered(viewName);

      // if the view is already rendered, do not re-render, unless it's being moved
      // to a new region
      // in either case just call the view's update function if it has one
      if (view.isRendered && (view.mountedAt === el)) {

        if (typeof view.controller.update === 'function') {
          view.controller.update(viewParams)
        }

      } else {
        // remove any views that are rendered in the reg
        this._removeViewAtRegion(el);
        if (view.isRendered && (view.mountedAt !== el)) {
          this.removeViews(viewName);
        }
        initializeView(view, el, viewParams);
      }

    }, this);
  };

  return BackboneViewMediator;

})();

module.exports = BackboneViewMediator;
